/* eslint-disable */
import crypto from 'crypto';
import { mapState } from 'vuex';

import Disqus from "@/components/disqus/Disqus.vue";

export default {
    name: 'Archive',
    components: {
        Disqus
    },
    props: {
        scrolled: Number,
        bgHeight: Number
    },
    data: () => ({
        password: null,
        readTime: 0,
        // Notification
        notification: false,
        notificationVisible: false,
        notificationMsg: '',
        notificationType: '',
        // Locked
        unlocked: null,
        // TOC
        showToc: false,
        tocs: [],
        tocCurrent: null,
        // 
        likeNum: 0,
        liked: false,
        //
        commentNum: 0
    }),
    fetch: async function ({ store, route }) {
        const isImplicit = route.matched[0].props.default.isImplicit
        const sourceOrSlug = isImplicit ? route.path.replace(/^\/pages\/?/, '') : route.params.slug;
        await store.dispatch('archive/fetchArchiveTarget', { isImplicit, sourceOrSlug });
    },
    beforeRouteUpdate: async function (to, from, next) {
        const { fetch } = this.$options;
        if (fetch) {
            try {
                this.$data.search = '';
                await fetch({ store: this.$store, route: to });
            } catch (error) {
                next(error);
            }
        } else {
            next();
        }
    },
    created: async function () {
        const that = this;

        document.title = this.target.title + ' - ' + that.$store.state.global.hexoConfig.site.title;

        if (that.target.locked) {
            const unlockedArchiveList = localStorage.unlockedArchiveList ? JSON.parse(localStorage.unlockedArchiveList) : [];
            unlockedArchiveList.forEach(async element => {
                if (element.slug == that.target.slug && element.password == that.target.password) {
                    that.unlocked = true;
                    that.$store.dispatch('archive/switchArchiveLockedStatus');

                    const liked = localStorage.liked ? JSON.parse(localStorage.liked) : [];
                    let count = liked.filter(element => element.slug == that.target.slug && element.title == that.target.title).length;
                    if (count !== 0) {
                        that.liked = !that.liked;
                    }
                }
            });
        } else {
            const liked = localStorage.liked ? JSON.parse(localStorage.liked) : [];
            let count = liked.filter(element => element.slug == that.target.slug && element.title == that.target.title).length;
            if (count !== 0) {
                that.liked = !that.liked;
            }
        }

        let likeNum = await that.$axios.get(process.env.VUE_APP_AIM_API + '/Common/Counter/Hexo', {
            params: {
                type: 'like',
                title: that.target.title,
                url: (this.$store.state.global.hexoConfig.url.url.indexOf('/') == -1 ? that.$store.state.global.hexoConfig.url.url : that.$store.state.global.hexoConfig.url.url + '/') + that.target.slug
            }
        })
        that.likeNum = likeNum.data.data.like;
    },
    beforeMount: function () {
        this.createMathJax();
    },
    mounted: function () {
        const that = this;

        that.$nextTick(() => {
            if (document.getElementById('postContent')) {
                that.setContent();

                if (window.MathJax) {
                    window.MathJax.Hub.Queue(["Typeset", window.MathJax.Hub]);
                }
            }
        });
    },
    computed: {
        ...mapState({
            dateFormat: (state) => state.global.hexoConfig.dateTimeFormat.date_format,
            timeFormat: (state) => state.global.hexoConfig.dateTimeFormat.time_format,
            disqusOptions: (state) => state.global.themeConfig.disqus,
            target: (state) => state.archive.target
        })
    },
    methods: {
        createMathJax: function () {
            if (!window.MathJax) {
                (function () {
                    let script = document.createElement('script');
                    script.src = 'https://cdnjs.aim.moe/ajax/libs/mathjax/2.7.7/MathJax.js?config=TeX-AMS-MML_HTMLorMML';
                    script.async = true;
                    document.head.appendChild(script);
                })();
            }

            if (window.MathJax) {
                window.MathJax.Hub.Config({
                    showProcessingMessages: false,
                    messageStyle: "none",
                    tex2jax: {
                        inlineMath: [['$', '$'], ['\\(', '\\)']],
                        displayMath: [['$$', '$$'], ['\[', '\]']],
                        processEscapes: true,
                        processEnvironments: true,
                        skipTags: ['script', 'noscript', 'style', 'textarea', 'pre'],
                        TeX: {
                            equationNumbers: { autoNumber: "AMS" },
                            extensions: ["AMSmath.js", "AMSsymbols.js"]
                        }
                    }
                });
            }
        },
        verification: function () {
            let that = this;

            if (!this.password) {
                this.notificationType = 'error'
                this.notificationMsg = '输密码啊 啊喂！！！';
                this.notification = true;
                this.notificationVisible = true;
                setTimeout(() => that.notificationVisible = false, 5000);
                setTimeout(() => that.notification = false, 5500);
                return;
            }
            let passwd = crypto.createHmac('sha256', '🎉').update(
                crypto.createHash('md5').update(
                    crypto.createHash('md5').update(
                        crypto.createHash('md5').update(this.password + '(/ω＼)').digest('hex') + '( ´▽｀)').digest('hex') + 'o(^▽^)o').digest('hex')).digest("hex");
            if (passwd == this.target.password) {
                if (typeof localStorage.unlockedArchiveList !== "string") {
                    localStorage.unlockedArchiveList = JSON.stringify([]);
                }
                let arr = JSON.parse(localStorage.unlockedArchiveList);
                arr.push({
                    slug: this.target.slug,
                    password: passwd
                });
                localStorage.unlockedArchiveList = JSON.stringify(arr);
                this.unlocked = true;
                that.$store.dispatch('archive/switchArchiveLockedStatus');
                setTimeout(() => that.setContent(), 500);
                this.notificationType = 'success'
                this.notificationMsg = '解锁成功';
                this.notification = true;
                this.notificationVisible = true;
                setTimeout(() => that.notificationVisible = false, 5000);
                setTimeout(() => that.notification = false, 5500);
            } else {
                this.notificationType = 'error'
                this.notificationMsg = '喂！密码错啦～';
                this.notification = true;
                this.notificationVisible = true;
                setTimeout(() => that.notificationVisible = false, 5000);
                setTimeout(() => that.notification = false, 5500);
                return;
            }
        },
        setContent: function () {
            let that = this;

            that.readTime = Math.round(document.getElementsByClassName(that.$style.postBody)[0].innerText.length / 400) > 1 ?
                Math.round(document.getElementsByClassName(that.$style.postBody)[0].innerText.length / 400) : 1;

            that.generateToc();
            that.imageGrid();

            if (window.MathJax) {
                window.MathJax.Hub.Queue(function () {
                    var all = window.MathJax.Hub.getAllJax(), i;
                    for (i = 0; i < all.length; i += 1) {
                        all[i].SourceElement().parentNode.className += ' has-jax';
                    }
                });
            }
        },
        imageGrid: function () {
            let that = this;
            document.querySelectorAll('.imageGrid').forEach((heading, i) => {
                heading.querySelectorAll('img').forEach((heading, j) => {
                    let width = Math.round(heading.getBoundingClientRect().width),
                        height = Math.round(heading.getBoundingClientRect().height);

                    if (height == 0) {
                        setTimeout(() => that.imageGrid(), 0);
                        return;
                    }

                    heading.parentNode.style.width = 200 * width / height + "px";
                    heading.parentNode.style.flexGrow = 200 * width / height;
                })
            })
        },
        tocTo: function (scrollTo) {
            window.scrollTo({
                top: scrollTo,
                behavior: 'smooth',
            })
        },
        generateToc: function () {
            let that = this;
            let chapters = [];
            let previous = 1;
            let level = 0;
            let headings = [];

            document.querySelectorAll('#postContent h1, #postContent h2, #postContent h3, #postContent h4, #postContent h5, #postContent h6').forEach((heading, i) => {
                if (!heading.id) {
                    heading.id = heading.textContent;
                }

                let current = parseInt(heading.tagName.replace(/[h]/i, ''), 10);
                let pid = -1;

                // 当前标题的（标题标签）序号 > 前一个标题的序号：两个相连的标题是父标题 -> 子标题关系；
                // h2 （前一个标题）
                // h3 （当前标题）
                if (current > previous) {
                    level += 1;

                    // 第一层级的 pid 是 -1
                    if (level === 1) {
                        pid = -1;
                    } else {
                        pid = i - 1;
                    }
                    // 当前标题的（标题标签）序号 = 前一个标题的序号
                    // h2 （前一个标题）
                    // h2 （当前标题）
                    // 当前标题的（标题标签）序号 < 前一个标题的序号，并且当前标题序号 > 当前的级别
                    // h2
                    // h4 （前一个标题）
                    // h3 （当前标题：这种情况我们还是任务 h3 是 h2 的下一级章节）
                } else if (current === previous || (current < previous && current > level)) {
                    // H1 的层级肯定是 1
                    if (current === 1) {
                        level = 1;
                        pid = -1;
                    } else {
                        pid = chapters[i - 1].pid;
                    }
                } else if (current <= level) {
                    // H1 的层级肯定是 1
                    if (current === 1) {
                        level = 1;
                    } else {
                        level = level - (previous - current);

                        if (level <= 1) {
                            level = 1;
                        }
                    }

                    // 第一级的标题
                    if (level === 1) {
                        pid = -1;
                    } else {
                        // 虽然看上去差点，不过能工作啊
                        pid = _generatePid(chapters, previous - current, i);
                    }
                }

                previous = current;

                chapters.push({
                    id: i,
                    pid: pid,
                    level: level,
                    tid: 'toc-' + i + '-' + heading.id,
                    textContent: heading.textContent,
                    href: window.location.origin + window.location.pathname + '#' + heading.id,
                    offsetTop: document.querySelector('#' + heading.id).offsetTop - (that.bgHeight / 2) + 180
                });

                if (i === 0) {
                    that.tocCurrent = chapters[0];
                }
            });
            that.tocs = headings;

            chapters.forEach(element => {
                if (element.pid == -1) {
                    headings.push(element);
                } else {
                    if (typeof headings[element.pid].children === 'undefined') {
                        headings[element.pid].children = [];
                    }
                    headings[element.pid].children.push(element);
                }
            });

            window.addEventListener('scroll', (e) => {
                if (!document.getElementsByClassName(that.$style.postBody).length) {
                    return;
                }
                let scrollTop = window.scrollY,
                    bottom = document.getElementsByClassName(that.$style.postBody)[0].offsetTop + document.getElementsByClassName(that.$style.postBody)[0].getBoundingClientRect().height;
                let currents = chapters.filter((heading, i, arr) => {
                    let next = arr[i + 1],
                        nextScrollTop;

                    if (!next) {
                        nextScrollTop = bottom;
                    } else {
                        nextScrollTop = next.offsetTop - (window.innerHeight / 5);
                    };

                    let scrollTop = window.scrollY + (window.innerHeight / 2);
                    return heading.offsetTop <= scrollTop && nextScrollTop > window.scrollY;
                });
                that.tocCurrent = currents[0] || chapters[0] || null;
            }, true);

            let _generatePid = (chapters, differ, index) => {
                let pid;
                // 最大只有 5 级的差距
                switch (differ) {
                    case 2:
                        pid = chapters[chapters[chapters[index - 1].pid].pid].pid;
                        break;
                    case 3:
                        pid = chapters[chapters[chapters[chapters[index - 1].pid].pid].pid].pid;
                        break;
                    case 4:
                        pid = chapters[chapters[chapters[chapters[chapters[index - 1].pid].pid].pid].pid].pid;
                        break;
                    case 5:
                        pid = chapters[chapters[chapters[chapters[chapters[chapters[index - 1].pid].pid].pid].pid].pid].pid;
                        break;
                    default:
                        pid = chapters[chapters[index - 1].pid].pid;
                        break;
                }

                return pid;
            }

            return headings;
        },
        clickAwesome: async function () {
            if (this.liked) {
                let likeNum = await this.$axios.delete(process.env.VUE_APP_AIM_API + '/Common/Counter/Hexo', {
                    params: {
                        type: 'like',
                        title: this.target.title,
                        url: (this.$store.state.global.hexoConfig.url.url.indexOf('/') == -1 ? this.$store.state.global.hexoConfig.url.url : this.$store.state.global.hexoConfig.url.url + '/') + this.target.slug
                    }
                })
                this.likeNum = likeNum.data.data.like;
                this.liked = !this.liked;

                let arr = JSON.parse(localStorage.liked);
                let newArr = arr.filter(element => element.slug !== this.target.slug && element.title !== this.target.title)
                localStorage.liked = JSON.stringify(newArr);
            } else {
                let likeNum = await this.$axios.post(process.env.VUE_APP_AIM_API + '/Common/Counter/Hexo', {
                    params: {
                        type: 'like',
                        title: this.target.title,
                        url: (this.$store.state.global.hexoConfig.url.url.indexOf('/') == -1 ? this.$store.state.global.hexoConfig.url.url : this.$store.state.global.hexoConfig.url.url + '/') + this.target.slug
                    }
                })
                this.likeNum = likeNum.data.data.like;
                this.liked = !this.liked;

                if (typeof localStorage.liked !== "string") {
                    localStorage.liked = JSON.stringify([]);
                }
                let arr = JSON.parse(localStorage.liked);
                arr.push({
                    slug: this.target.slug,
                    title: this.target.title,
                });
                localStorage.liked = JSON.stringify(arr);
            }
        },
        isObjectEmpty: function (value) {
            return Object.keys(value).length === 0;
        }
    },
    watch: {

    }
}